.kg_panel {
  padding: 12px 20px;
}
.kg_panel .kg_item_li {
  padding: 12px 16px;
  background-color: #f5f5f5;
  cursor: pointer;
  margin-bottom: 12px;
}
.kg_panel .kg_item_li .kg_item_li_title {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 600;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.kg_panel .kg_item_li .kg_item_li_content {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #666666;
  letter-spacing: 0;
  text-align: justify;
  font-weight: 400;
  margin-top: 8px;
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
}
.kg_panel .kg_item_li:hover {
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.18);
}
.kg_panel .kg_item_li:hover .kg_item_li_title {
  color: var(--theme-color);
}
